'use client'
import React, { useState } from 'react'
import { Producto, Servicio, VentaServicio } from '../../../entities/servicios'

const serviciosMock: Servicio[] = [
  { idServicio: 1, nombre: 'Mantenimiento', costo: 150 },
  { idServicio: 2, nombre: 'Instalaci√≥n', costo: 200 }
]

const productosMock: Producto[] = [
  { idProducto: 1, nombre: 'Router' },
  { idProducto: 2, nombre: 'Cable' }
]

interface Props {
  onRegistrarVenta: (venta: VentaServicio) => void
}

export default function ServiceForm({ onRegistrarVenta }: Props) {
  const [cliente, setCliente] = useState('')
  const [tecnico, setTecnico] = useState('')
  const [servicios, setServicios] = useState<Servicio[]>([])
  const [productos, setProductos] = useState<{ producto: Producto; cantidad: number }[]>([])

  const agregarServicio = (servicio: Servicio) => {
    if (!servicios.find(s => s.idServicio === servicio.idServicio)) {
      setServicios([...servicios, servicio])
    }
  }

  const agregarProducto = (producto: Producto) => {
    const existente = productos.find(p => p.producto.idProducto === producto.idProducto)
    if (existente) {
      setProductos(productos.map(p =>
        p.producto.idProducto === producto.idProducto
          ? { ...p, cantidad: p.cantidad + 1 }
          : p
      ))
    } else {
      setProductos([...productos, { producto, cantidad: 1 }])
    }
  }

  const calcularTotal = () => {
    const totalServicios = servicios.reduce((sum, s) => sum + s.costo, 0)
    const totalProductos = productos.reduce((sum, p) => sum + p.cantidad * 20, 0)
    return totalServicios + totalProductos
  }

  const handleSubmit = () => {
    if (!cliente || !tecnico || servicios.length === 0) {
      alert('Cliente, t√©cnico y al menos un servicio son obligatorios')
      return
    }

    const venta: VentaServicio = {
      idVentaServicio: Date.now(),
      cliente: { idCliente: Date.now(), nombre: cliente },
      tecnico: { idUsuario: Date.now(), nombre: tecnico },
      descuento: 0,
      costoTotal: calcularTotal(),
      fechaRegistro: new Date().toISOString(),
      detallesServicio: servicios.map((s, i) => ({
        idDetalleServicio: i + 1,
        servicio: s,
        costoTotal: s.costo,
        descuento: 0
      })),
      productos: productos.map((p, i) => ({
        idProductoDetalleVenta: i + 1,
        producto: p.producto,
        cantidad: p.cantidad,
        precioTotal: p.cantidad * 20,
        descuento: 0
      }))
    }

    onRegistrarVenta(venta)
    setCliente('')
    setTecnico('')
    setServicios([])
    setProductos([])
  }

  return (
    <div className="box-white">
      <h2 className="section-title">üìù Registro de Servicio</h2>

      <input
        className="search-input mb-2"
        placeholder="Nombre del cliente"
        value={cliente}
        onChange={(e) => setCliente(e.target.value)}
      />
      <input
        className="search-input mb-4"
        placeholder="Nombre del t√©cnico"
        value={tecnico}
        onChange={(e) => setTecnico(e.target.value)}
      />

      {/* Agregar Servicio */}
      <div className="mb-4">
        <h4>Servicios disponibles:</h4>
        <div className="filter-group">
          {serviciosMock.map(s => (
            <button key={s.idServicio} className="primary-btn" onClick={() => agregarServicio(s)}>
              + {s.nombre} (S/ {s.costo})
            </button>
          ))}
        </div>
      </div>

      {/* Tabla Servicios */}
      {servicios.length > 0 && (
        <table className="products-table mb-4">
          <thead>
            <tr><th>Servicio</th><th>Costo</th></tr>
          </thead>
          <tbody>
            {servicios.map(s => (
              <tr key={s.idServicio}>
                <td>{s.nombre}</td>
                <td>S/ {s.costo}</td>
              </tr>
            ))}
          </tbody>
        </table>
      )}

      {/* Agregar Producto */}
      <div className="mb-4">
        <h4>Productos adicionales:</h4>
        <div className="filter-group">
          {productosMock.map(p => (
            <button key={p.idProducto} className="primary-btn" onClick={() => agregarProducto(p)}>
              + {p.nombre}
            </button>
          ))}
        </div>
      </div>

      {/* Tabla Productos */}
      {productos.length > 0 && (
        <table className="products-table mb-4">
          <thead>
            <tr><th>Producto</th><th>Cantidad</th><th>Subtotal</th></tr>
          </thead>
          <tbody>
            {productos.map(p => (
              <tr key={p.producto.idProducto}>
                <td>{p.producto.nombre}</td>
                <td>{p.cantidad}</td>
                <td>S/ {p.cantidad * 20}</td>
              </tr>
            ))}
          </tbody>
        </table>
      )}

      <div className="mt-3 font-bold text-lg">Total: S/ {calcularTotal()}</div>
      <button className="primary-btn mt-4" onClick={handleSubmit}>Registrar Venta</button>
    </div>
  )
}
